<template>
    <div class="listdetails">
        <div class="listdetailsbox">
            <div
                class="beforebox"
                :style="{
                    backgroundImage: 'url(' + listData.coverImgUrl + ')'
                }"
            ></div>
            <!-- 白色背景 -->
            <div class="whiteBox"></div>
            <div class="top flexbetween">
                <div class="left">
                    <van-icon
                        name="down"
                        @click="getback"
                        class="down"
                        size="20"
                    />
                    <span>歌单</span>
                </div>
                <div class="right">
                    <van-icon name="search" class="search" size="20" />
                    <van-icon name="ellipsis" size="20" class="ellipsis" />
                </div>
            </div>
            <div class="body">
                <div class="imgbox">
                    <img :src="listData.coverImgUrl" alt />
                </div>
                <div class="Txtbox">
                    <strong>{{ listData.name }}</strong>
                    <p>{{ listData.description }}</p>
                    <p>{{ listData.createTime | formatDate("YYYY/MM/DD") }}</p>
                </div>
            </div>
            <div class="foot">
                <div class="footlist">
                    <!-- 加20px -->
                    <van-sticky
                        class="stickyboxTop"
                        :offset-top="40"
                        @scroll="scrolltopchange"
                    >
                        <div class="footlisttop flexbetween">
                            <div class="playbox">
                                <van-icon
                                    name="play-circle-o"
                                    class="play-o"
                                    size="20"
                                />
                                <strong>播放全部</strong>
                                <span>(共{{ songNum }}首)</span>
                            </div>
                            <div class="livebox">
                                <span>+收藏</span>
                            </div>
                        </div>
                        <div class></div>
                    </van-sticky>
                    <div class="footlistbody-box" ref="footlistbodybox">
                        <div class="footlistbody">
                            <div
                                class="footlistbodyitem flexbetween"
                                v-for="(item, index) in songData"
                                :key="index"
                                @click="clicksetUrl(item)"
                            >
                                <div class="itemleft">
                                    <span class="num">{{ index + 1 }}</span>
                                    <div class="namebox">
                                        <p>{{ item.name }}</p>
                                        <span class="one-ellipsis"
                                            >{{ item.songAr }}--{{
                                                item.al.name
                                            }}</span
                                        >
                                    </div>
                                </div>
                                <div class="itemright">
                                    <van-icon
                                        name="ellipsis"
                                        size="20"
                                        class="ellipsis"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import "../assets/less/listdetails.less";
import { createNamespacedHelpers } from "vuex";
const {
    mapState: musicState,
    mapActions: musicActions
} = createNamespacedHelpers("MusicAudioStore");
export default {
    created() {
        this.songlistId = this.$route.query.id;
        if (this.songlistId) {
            this.getsonglistData();
        }
        //设置音乐组件
        if (this.clickSong) {
            this.setaudioxy(1);
        }
    },
    data() {
        return {
            songlistId: "",
            // 歌单详情
            listData: [],
            //歌曲详情
            songData: [],
            //歌曲数目
            songNum: ""
        };
    },
    computed: {
        ...musicState(["audioData", "songListCache", "detailbool", "clickSong"])
    },
    methods: {
        ...musicActions([
            "setMusicUrl",
            "setaudioData",
            "setaudioxy",
            "seticonNum",
            "setsongData",
            "setSongListData",
            "setSongListCache",
            "setClickSong"
        ]),

        //返回
        getback() {
            this.$router.go(-1);
            //清除歌单缓存
            this.setSongListCache("");
        },

        //歌单框架显示滑动
        scrolltopchange(e) {
            let foot = document.querySelector(".foot");
            if (e.scrollTop >= foot.offsetTop - 45) {
                //加20px
                this.$refs.footlistbodybox.style.overflowY = "auto";
            } else {
                this.$refs.footlistbodybox.style.overflowY = "hidden";
            }
        },

        //获取歌单数据
        getsonglistData() {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/playlist/detail",
                params: {
                    id: this.songlistId
                }
            })
                .then(result => {
                    this.$toast.clear();
                    this.listData = result.data.playlist;
                    this.songNum = this.listData.trackIds.length;

                    let songIds = [];
                    result.data.playlist.trackIds.map(v => {
                        songIds.push(v.id);
                    });
                    songIds = songIds.join(",");
                    //有歌单缓存就直接使用
                    if (this.songListCache.length != 0) {
                        this.songData = this.songListCache;
                    } else {
                        //无歌单缓存就获取
                        //获取歌单歌曲详情
                        this.getsongDatabyId(songIds);
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取歌单歌曲详情
        getsongDatabyId(ids) {
            let idsdata = ids;
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/song/detail",
                params: {
                    ids: idsdata
                }
            })
                .then(result => {
                    this.$toast.clear();
                    let data = [];
                    data = result.data.songs;

                    // 获取歌单的播放url
                    data.map((v, index) => {
                        //设置歌手名称
                        let songar = "";
                        v.ar.map(arv => {
                            songar += arv.name + "/";
                        });
                        v.songAr = songar.slice(0, -1);
                        //设置播放音乐链接
                        v.palyurl =
                            "https://music.163.com/song/media/outer/url?id=" +
                            v.id +
                            ".mp3";
                        //设置active
                        v.isActive = false;
                        //获取歌手专辑（获取歌手图片）
                        this.getSingerAlbum(v, v.ar[0].id);

                        //设置总时间
                        let m = Math.floor((v.dt / 1000 / 60) % 60);
                        let s = Math.floor((v.dt / 1000) % 60);
                        v.totalTime = m + "." + s;

                        //设置歌词
                        //获取歌词
                        this.getSingerData(v, v.id);
                    });
                    this.songData = data;
                    // 设置歌单数据缓存
                    this.setSongListCache(this.songData);
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取歌词
        getSingerData(v, id) {
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/lyric",
                params: {
                    id
                }
            })
                .then(result => {
                    //纯音乐，歌词无内容
                    if (result.data.nolyric || result.data.lrc.lyric == "") {
                        let data = [
                            {
                                nolyric: true,
                                title: "纯音乐，请欣赏",
                                isActive: false
                            }
                        ];
                        v.songWordData = data;
                        return;
                    }
                    if (result.data) {
                        let reg = "\\[.*\\]";
                        // let songword = "";
                        // songword = result.data.lrc.lyric.replace(/\s+/g, "");

                        let sonArray = result.data.lrc.lyric.split(/[\r\n]/g);

                        let data = [];
                        for (let i = 0; i < sonArray.length; i++) {
                            //包括[]
                            if (sonArray[i] == "") {
                                continue;
                            }
                            let timekey = sonArray[i].match(reg)[0];
                            //没有包括[]
                            // 判断是否含有[00:00]格式
                            let timekey2 = "";
                            if (sonArray[i].indexOf(0) > -1) {
                                timekey2 = sonArray[i].match(
                                    /[0-9]\d:[0-9]\d/
                                )[0];
                            } else {
                                timekey2 = "";
                            }
                            let timeLength = timekey.split("").length;
                            let songText = sonArray[i].slice(timeLength);
                            //除去歌词是空的
                            if (songText == "") {
                                continue;
                            }
                            data.push({
                                time: timekey2,
                                title: songText,
                                isActive: false
                            });

                            // vue手动刷新页面
                            // this.$forceUpdate();
                        }

                        v.songWordData = data;
                    }
                    this.$toast.clear();
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //获取歌手专辑
        getSingerAlbum(v, id) {
            if (id == "0") {
                return;
            }
            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });
            this.axios({
                method: "get",
                url: "/artist/album",
                params: {
                    id
                }
            })
                .then(result => {
                    this.$toast.clear();
                    if (result.status == 200) {
                        v.singerImg = result.data.artist.picUrl;
                    }
                })
                .catch(err => {
                    this.$toast.clear();
                });
        },

        //点击设置音乐播放
        clicksetUrl(item) {
            //设置播放组件显示
            this.setClickSong(true);
            this.setaudioxy(1);

            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner"
            });

            //传值到state

            this.setsongData(item);
            this.seticonNum(1);
            let data = this.songData.concat();
            this.setSongListData(data);
        }
    }
};
</script>

<style lang="less" scoped>
</style>